@media screen and (max-width: 1700px){
  .section{
    &-catalog{
      .container{
        position: relative;
        z-index: 5;
      }
    }
  }
}

@media screen and (max-width: 1280px){
  .container{
    padding:0 15px;
  }
  .catalog{
    &-list{
      justify-content: space-around;
    }
  }
  .phone-link{
    &_header{
      margin:0 25px;
    }
  }
  .slider{
    &-next{
      right:-5px;
    }
    &-prev{
      left: -5px;
    }
  }
}
@media screen and (max-width: 1024px) {
  .news{
    &__img{
      width: 200px;
      height: 200px;
      img{
        object-fit: contain;
      }
    }
    .btn{
      margin-top: 20px;
    }
  }
  .gallery{
    &-list{
      flex-wrap: wrap;
      &__item{
        width: calc(33% - 5px);
        margin-bottom: 15px;
      }
    }
  }
  .sub-list{
    left: -180px;
  }
}
@media screen and (max-width: 768px){
  .header{
    &__row{
      padding: 0;
      height: 80px;
    }
    .container{
      padding: 0;
    }
    .logo{
      margin-left: 15px;
    }
    .instagram-link{
      margin-left: auto;
      margin-right: 15px;
    }
  }
  .main-nav{
    &_header{
      position: absolute;
      top: 0;
      right: 0;
      width: 260px;
      background-color: $colorOrange;
      border: solid 1px #dadada;
      border-top: none;
      border-right: none;
      opacity: 0;
      transform: translateY(-100%);
      transition: .3s all linear;
      overflow: hidden;
      &.active{
        transform: translateY(0);
        opacity: 1;
        z-index: 999;
      }
    }
    &__list{
      flex-direction: column;
      align-items: flex-start;
    }
    &__item{
      width: 100%;
      &_phone,&_close {
        width: 100%;
        display: flex;
        padding: 20px 0;
      }
      &_phone{
        background-color: $colorRed;
        justify-content: center;
      }
      &_close{
        background-color: rgba(#ffffff,0.3);
      }
    }
    &__item + &__item{
      margin-left: 0;
      margin-top: 20px;
    }
    &__link{
      font-size: 15px;
      font-weight: 700;
      padding: 0 30px;
    }
  }
  .sub-list{
    position: relative;
    display: block;
    top: 0;
    left: 0;
    margin-top: 20px;
    height: unset;
    box-shadow: none;
    padding:15px;
    width: 100%;
    border:none;
    &__item + &__item{
      margin-left: 0;
      margin-top: 15px;
    }
    &__item{
      &::before{
        display: none;
      }
    }
    &__link{
      padding:0 30px;
    }
  }
  .phone-link{
    &_header{
      display: none;
    }
    &_footer{
      margin:15px 0 0 0;
    }
    &_nav{
      display: flex;
    }
  }
  .section{
    &-welcome{
      padding:180px 0 115px 0;
    }
  }
  .slider{
    &-container{
      width: auto;
      &::after{
        width: calc(100% - 25px);
      }
    }
    &-welcome{
      width: calc(100% - 30px);
      &__title{
        font-size: 48px;
        span{
          font-size: 31px;
        }
      }
    }
  }
  .contacts{
    &__row{
      flex-direction: column;
      .column + .column{
        margin-left: 0;
        margin-top: 20px;
      }
      .column{
        &:nth-child(1),&:nth-child(2) {
          width: 100%;
        }
      }
    }
    &__img{
      margin:50px auto auto auto;
    }
    &__map{
      height: 500px;
    }
  }
  .news{
    &-single{
      flex-direction: column;
      .news{
        &__img{
          margin-bottom: 20px;
          width: 100%;
          height: 350px;
        }
      }
    }
    &-list{
      &__item{
        flex-basis: 100%;
        width: 100%;
      }
    }
    &__img{
      width: 50%;
      img{
        object-fit: cover;
      }
    }
  }
  .category{
    &-list{
      flex-wrap: wrap;
      justify-content: center;
      &__item{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-basis: 25%;
        margin-bottom: 30px;
      }
    }
  }
  .shares{
      &__img{
        img{
          object-fit: fill;
        }
      }
  }
  .footer{
    &__row{
      align-items: flex-start;
      justify-content: center;
      flex-direction: column;
    }
    &__address{
      margin-left: unset;
    }
    .instagram-link{
      display: none;
    }
    .main-nav{
      display: none;
    }
  }
  .bar{
    &_header{
      display: flex;
    }
  }
}

@media screen and (max-width: 500px) {
   .slider{
     &-container{
       &::after{
         top: 15px;
         left: 15px;
         width: calc(100% - 30px);
         height: calc(100% - 30px);
       }
     }
     &-welcome{
       width: 100%;
       &__item{

       }
       &__btn{
         margin:45px auto auto auto;
       }
       &__content{
         padding:60px 0;
         text-align: center;
       }
     }
   }
  .news{
    flex-direction: column;
    &__img{
      width: 100%;
      margin-bottom: 20px;
      height: 250px;
    }
  }
  .gallery{
    &-list{
      &__item{
        width: calc(50% - 5px);
        margin-bottom: 10px;
      }
    }
  }
}

@media screen and (max-width: 425px) {
  .slider{
    &-welcome{
      &__title{
        font-size: 36px;
        span{
          font-size: 23px;
        }
      }
      &__item{
        height: 270px;
      }
      &__btn{
        left: 50%;
        margin-left: -113px;
      }
    }
  }
  .shares{
    &-list{
      &__img{
        display: none;
      }
      &__item{
        height: unset;
        padding-bottom: 50px;
        &::before{
          top: 5px;
          left: 5px;
          width: calc(100% - 10px);
          height: calc(100% - 10px);
        }
      }
    }
  }
  .contacts{
    &__img{
      width: 100%;
    }
  }
  .news{
    &-single{
      .news{
        &__img{
          height: 250px;
        }
      }
    }
  }
  .category{
    &-list{
      justify-content: space-around;
    }
  }
  .text{
    &_size{
      &-middle-big{
        font-size: 25px;
        line-height: 28px;
      }
      &_big{

      }
    }
  }
}

@media screen and (max-width: 375px){
  .slider{
    &-welcome{
      &__title{
        font-size: 28px;
        span{
          font-size: 18px;
        }
      }
    }
  }
}

@media screen and (max-width: 320px) {
  .logo{
    font-size: 25px;
  }
  .section{
    &__title{
      font-size: 24px;
    }
  }
  .slider{
    &-container{
      &::after{
        top: 5px;
        left: 5px;
        width: calc(100% - 10px);
        height: calc(100% - 10px);
      }
    }
  }
  .gallery{
    &-list{
      &__item{
        height: 150px;
      }
    }
  }
}