.gallery{
  &-list{
    margin:60px 0;
    display: flex;
    justify-content: space-between;
    &__item{
      width: 200px;
      height: 200px;
    }
    &__img{
      width: 100%;
      height: 100%;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}