body{
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
}

.container{
  max-width: 1280px;
  width: 100%;
  margin: 0 auto;
}

.text+.text{
  margin-top: 25px;
}

.page-content{
  display: flex;
  flex-direction: column;
  .footer{
    margin-top: auto;
  }
}
.content{
  padding-top: 55px;
  .section__title{
    text-align: left;
  }
}

.phone-link{
  font-size: 24px;
  color:#ffffff;
  font-weight: 700;
  .icon{
    font-size: 18px;
    color:$colorOrange;
    margin-right: 10px;
  }
  &_header{
    margin: 0 25px 0 85px;
  }
  &_footer{
    margin:0 45px 0 30px;
  }
  &_nav{
    font-size: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    .icon{
      color:#ffffff;
      margin-right: 5px;
      font-size: 16px;
    }
  }
}

.instagram-link{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  background-color: $colorOrange;
  border-radius: 50%;
  .icon{
    color:#000000;
  }
}

.section{
  &-welcome{
    padding:305px 0 155px 0;
    background: url("../img/bckg/header_home.png") center center no-repeat;
    background-size: cover;
    &_sushi{
      background: url("../img/bckg/header_sushi.png") center center no-repeat;
      background-size: cover;
    }
  }
  &-catalog{
    padding: 60px 0 45px 0;
    position: relative;
    &::before,&::after{
      content: '';
      position: absolute;
      width: 200px;
      display: block;
      background-size: cover;
      background-position: center center;
      background-repeat: no-repeat;
    }
    &::before{
      right: 0;
      top:0;
      height: 500px;
      background-image: url("../img/bckg/food2.png");
    }
    &::after{
      left: 0;
      bottom: 200px;
      height: 700px;
      background-image: url("../img/bckg/food1.png");
    }
  }
  &-shares{
    padding:70px 0 85px 0;
    background: url("../img/bckg/footer.png") center center no-repeat;
    background-size: cover;
  }
  &__title{
    font-size: 30px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 45px;
    &_color{
      &-black{
        color:#000000;
      }
      &-white{
        color:#ffffff;
      }
    }
  }
}

.margin-small{
  margin-top: 30px;
}
.margin-smallest{
  margin:10px 0;
}

.text{
  &_size{
    &-smallest{
      font-size: 11px;
      color:#000000;
      line-height: 22px;
    }
    &-small{
      font-size: 14px;
    }
    &-middle{
      font-size: 16px;
    }
    &-middlest{
      font-size: 19px;
    }
    &-big{
      font-size: 25px;
      line-height: 31px;
    }
    &-middle-big{
      font-size: 30px;
      line-height: 41px;
    }
    &-biggest{
      font-size: 45px;
    }
  }
  &-transform{
    &_uppercase{
      text-transform: uppercase;
    }
  }
  &_color{
    &-red{
      color:$colorRed;
    }
    &-white{
      color:#ffffff;
    }
    &-black{
      color:#000000;
    }
  }
  &_weight{
    &-bold{
      font-weight: 700;
    }
  }
}
