.page-content{
  .header{
    position: relative;
    background-color: #080808;
    &__row{
      border:none;
      &::before,&::after{
        display: none;
      }
    }
  }
}
.logo{
  font-family: 'AGGloria';
  font-size: 30px;
  text-transform: uppercase;
  padding-bottom: 10px;
  border-bottom: 3px solid $colorOrange;
  color:#ffffff;
}

.header{
  position: absolute;
  width: 100%;
  z-index: 999;
  &__row{
    position: relative;
    display: flex;
    align-items: center;
    height: 110px;
    padding: 0 15px;
    border-bottom: 1px solid rgba(#cdcdcd,0.3);
    &::before,&::after{
      content: '';
      position: absolute;
      display: block;
      bottom: -1px;
      width: 40px;
      height: 1px;
    }
    &::before{
      background-color: rgba(#7f9800,.3);
      left: 50px;
    }
    &::after{
      background-color: rgba(#f43c3c,.3);
      left: 125px;
    }
  }
}

.bar{
  width: 30px;
  height: 20px;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  &_header{
    margin-right: 15px;
    display: none;
  }
  &__item{
    width: 100%;
    height: 2px;
    background-color: #ffffff;
  }
}

.main-nav{
  &_header{
    margin-left: auto;
  }
  &__list{
    display: flex;
    align-items: center;
  }
  &__item + &__item{
    margin-left: 35px;
  }
  &__item{
    &.has-sub-list{
      position: relative;
      &::before{
        position: absolute;
        left: 0;
        top: 0;
        content: '';
        width: 100%;
        height: 0;
        display: block;
        z-index: 0;
      }
      &:hover{
        &::before{
          height: 80px;
        }
        .sub-list{
          display: flex;
        }
      }
    }
    &_close,&_phone{
      display: none;
    }
  }
  &__link{
    font-size: 13px;
    color:#ffffff;
    transition: .3s color linear;
    text-transform: uppercase;
    z-index: 5;
    position: relative;
    &:hover{
      color:$colorOrange;
    }
  }
}

.sub-list{
  position: absolute;
  display: none;
  top: 61px;
  align-items: center;
  justify-content: center;
  height: 65px;
  box-shadow: 0px 6px 6px 0 rgba(0, 0, 0, 0.48);
  border: solid 1px #dadada;
  background-color: #ffffff;
  padding:0 15px;
  transition: 0.3s all ease-in-out;
  &__item + &__item{
    margin-left: 30px;
  }
  &__item{
    display: flex;
    align-items: center;
    &::before{
      content: '';
      display: block;
      border-radius: 50%;
      width: 8px;
      height: 8px;
      margin-right: 7px;
      background-color: $colorOrange;
      transition: 0.3s all ease-in-out;
    }
    &:hover{
      &::before{
        background-color: $colorRed;
      }
    }
    a{
      color:#000000;
      font-size: 13px;
      text-transform: uppercase;
      font-weight: 700;
    }
  }
}