.slider{
  &-container{
    position: relative;
    width: fit-content;
    &:after{
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      border:3px solid #ffffff;
      bottom: 25px;
      left: 25px;
      display: block;
      z-index: 3;
    }
  }
  &-welcome{
    width: 710px;
    position: relative;
    &__item{
      height: 315px;
      background-color: rgba($colorOrange,0.9);
      position: relative;

      &_pizza,&_sushi{
        &::before{
          content: '';
          position: absolute;
          right: 0;
          bottom: 0;
          display: block;
          width: 300px;
          height: 100%;
          background-position: center center;
          background-repeat: no-repeat;
          background-size: cover;
        }
      }
      &_pizza{
        &::before{
          background-image: url("../img/bckg/tile.png");
        }
      }
      &_sushi{
        &::before{
          background-image: url("../img/bckg/tile_sushi.png");
        }
      }
    }
    &__content{
      position: relative;
      z-index: 2;
      padding: 60px 0 0 60px;
    }
    &__title{
      color:#ffffff;
      font-size: 70px;
      text-transform: uppercase;
      font-weight: 700;
      span{
        display: block;
        font-size: 45px;
        margin-top: 5px;
      }
    }
    &__btn{
      position: absolute;
      bottom: 50px;
      left: 60px;
      z-index: 5;
    }
    .slick-dots{
      position: absolute;
      display: flex;
      align-items: center;
      left: 0;
      bottom: -50px;
      z-index: 99;
      li{
        width: 10px;
        height: 10px;
        background-color: #ffffff;
        border-radius: 50%;
        margin-right: 10px;
        transition: 0.3s all linear;
        &:hover{
          cursor: pointer;
        }
        button{
          text-indent: -9999px;
          overflow:hidden;
        }
        &.slick-active{
          background-color: $colorOrange;
        }
      }

    }
  }
}