.contacts{
  &-list{
    display: flex;
    flex-direction: column;
    &__item{
      font-size: 14px;
      color:#000000;
      a{
        font-size: 14px;
        color:#000000;
      }
      .icon{
        color:$colorOrange;
        margin-right: 10px;
      }
    }
    &__item + &__item{
      margin-top: 20px;
    }
  }
  &__img{
    margin-top: 50px;
    width: 100%;
    height: 375px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__row{
    display: flex;
    justify-content: flex-end;
    .column{
      &:nth-child(1){
        width: 30%;
      }
      &:nth-child(2){
        width: 70%;
        margin-left: 50px;
      }
    }
  }
  &__map{
    height: 100%;
    iframe{
      width: 100%;
      height: 100%;
    }
  }
}
