.news{
  display: flex;
  &-list{
    display: flex;
    flex-wrap: wrap;
    &__item{
      flex-basis: 50%;
      margin-bottom: 80px;
    }
  }
  &-single{
    .news{
      &__desc{
        text-align: justify;
      }
      &__img{
        width: 480px;
        height: 340px;
      }
    }
  }
  &__img{
    width: 285px;
    height: 205px;
    flex-shrink: 0;
    margin-right: 35px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__info{
    display: flex;
    flex-direction: column;
  }
  &__date{
    font-size: 12px;
    color:#878787;
    margin-bottom: 15px;
  }
  &__title{
    color:$colorOrange;
    font-size: 22px;
    font-weight: 700;
    margin-bottom: 25px;
  }
  &__desc{
    font-size: 14px;
    line-height: 20px;
    color:#000000;
  }
  .btn{
    margin-top: auto;
  }
}