.btn{
  display: flex;
  align-items: center;
  justify-content: center;
  &-news{
    width: 150px;
    height: 50px;
    border-radius: 12px;
    text-transform: uppercase;
    color:#000000;
    font-weight: 700;
    transition: 0.3s all ease-in-out;
    background-color: $colorOrange;
    &:hover{
      color:#ffffff;
      background-color:$colorRed;
    }
  }
  &_style{
    &-first{
      width: 215px;
      height: 45px;
      border-radius: 12px;
      border:2px solid #ffffff;
      color:#ffffff;
      text-transform: uppercase;
      font-weight: 700;
      transition: 0.3s all ease-in-out;
      &:hover{
        color:$colorOrange;
        background-color: #ffffff;
      }
    }
    &-second{
      width: 250px;
      height: 50px;
      margin:0 auto;
      border-radius: 12px;
      background-color: $colorOrange;
      text-transform: uppercase;
      border:1px solid transparent;
      color:#000000;
      font-weight: 700;
      transition: 0.3s all ease-in-out;
      &:hover{
        color:$colorOrange;
        background-color: transparent;
        border-color:$colorOrange;
      }
    }
  }
  &-close{
    position: absolute;
    width: 22px;
    height: 22px;
    right: 15px;
    top: 10px;
    &::after,&::before{
      content: '';
      position: absolute;
      height: 100%;
      width: 2px;
      display: block;
      background-color: #ffffff;
    }
    &::after{
      transform: rotate(45deg);
    }
    &::before{
      transform: rotate(-45deg);
    }
  }
}