.footer{
  background: #121212;
  &__row{
    display: flex;
    align-items: center;
    height: 115px;
  }
  &__address{
    margin-left: auto;
    color:#ffffff;
    display: flex;
    align-items: flex-end;
    address{
      font-style: normal;
    }
    .icon{
      font-size: 20px;
      margin-right: 10px;
      color:$colorOrange;
    }
  }
}