.product{
  border-bottom: 1px solid #cdcdcd;
  transition: 0.3s all ease-in-out;
  &:hover{
    cursor: pointer;
    border-bottom-color: $colorRed;
  }
  &__img{
    width: 100%;
    height: 286px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__info{
    padding:20px 0;
  }
  &__title {
    font-weight: 700;
    font-size: 22px;
    color: $colorOrange;
  }
  &__desc{
    margin:20px 0 15px 0;
    font-size: 14px;
    line-height: 20px;
    color:#000000;
  }
  &__footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__feature{
    color:#878787;
  }
  &__price{
    font-size: 20px;
    font-weight: 700;
    color:$colorRed;
  }
}