.shares{
  &-list{
    &__item{
      height: 280px;
      background-color: $colorOrange;
      position: relative;
      margin:0 20px;
      &::before{
        content:'';
        position: absolute;
        display: block;
        width: calc(100% - 30px);
        height: calc(100% - 30px);
        top: 15px;
        left: 15px;
        z-index: 2;
        border:3px solid rgba(#ffffff,.8);
      }
    }
    &__img{
      position: absolute;
      right: 0;
      top: 0;
      height: 100%;
      width: 50%;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &__info{
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 45px 0 0 45px;
    }
  }
}
.slider{
  &-next,&-prev{
    background-color: transparent;
    position: absolute;
    top: 50%;
    margin-top: -13px;
    z-index: 99;
    .fa{
      color:#ffffff;
      font-size: 35px;
    }
  }
  &-next{
    right: -25px;
  }
  &-prev{
    left: -25px;
  }
}