.category{
  &-list{
    padding:45px 0;
    border-bottom: 1px solid rgba(#7d7d7d,.25);
    display: flex;
    justify-content: space-between;
    &__item{
      width: 100px;
      &:hover{
        .category-list__img{
          background-color: $colorRed;
        }
      }
    }
    &__img{
      width: 100px;
      height: 100px;
      border-radius: 50%;
      background-color: $colorOrange;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: .3s background-color linear;
      .icon{
        font-size: 45px;
        color:#ffffff;
        &-pizza{
          font-size: 60px;
        }
      }
    }
    &__title{
      text-align: center;
      font-size: 15px;
      color:#000000;
      margin-top: 20px;
      font-weight: 700;
      text-transform: uppercase;
    }
  }
}